.project {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: $size-5;
  padding: 0;
  @media (min-width: $breakpoint-2) {
    flex-direction: row;
  }
}

.project__image-box {
  background-size: cover;
  border-radius: $radius-2;
  box-shadow: $shadow-3;
  height: $size-11 - 1.4;
  margin: 0;
  padding: 0;
  width: 100%;
  @media (min-width: 100.4rem) and (max-width: 104.4rem) {
    height: $size-13;
  }
  @media (min-width: $breakpoint-2) {
    height: $size-12;
    transition: background-image 0.25s ease;
    width: $size-14;
  }
}

.project__image-box--meal {
  background-image: url(/images/meal-to-list.jpg);
}

.project__image-box--maze {
  background-image: url(/images/maze.jpg);
}

.project__image-box--pantry {
  background-image: url(/images/another-image.jpg);
}

.project__image-box:hover {
  @media (min-width: $breakpoint-2) {
    background-color: $primary-6;
    background-image: none;
  }
}

.project__link-box {
  background: $primary-6;
  border-radius: $radius-2;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  margin: 0;
  opacity: 0;
  transition: opacity 0.25s ease;
  width: 100%;
  @media (max-width: $breakpoint-2 - 1) {
    display: none;
  }
}

.project__link-box:hover {
  opacity: 1;
}

.project__link {
  background: $primary-6;
  border: $size-1 solid $secondary-4;
  border-radius: $radius-2;
  color: $white;
  font-family: $text;
  font-size: $size-4;
  margin: $size-3 $size-9;
  outline: none;
  padding: $size-2;
  text-align: center;
  text-decoration: none;
  transition: background 0.25s ease;
}

.project__link:hover {
  background: $primary-5;
}

.project__link-box--mobile {
  @extend .project__link-box;
  background: $primary-7;
  display: flex;
  flex-direction: row;
  margin: $size-3 0 $size-5 0;
  opacity: 1;
  @media (min-width: $breakpoint-2) {
    display: none;
  }
}

.project__link--mobile {
  @extend .project__link;
  background: $primary-7;
  border: $size-1 solid $primary-6;
  margin: $size-2 $size-2;
  padding: $size-2 $size-4;
}

.project__info-box {
  align-self: flex-start;
  display: flex;
  flex-direction: column;
  margin: 0;
  margin-top: $size-5;
  padding: 0;
  width: 100%;
  @media (min-width: $breakpoint-2) {
    margin: 0;
    margin-left: $size-7;
    width: $size-13;
  }
}

.project__name {
  border-bottom: $size-1 solid $secondary-4;
  color: $primary-1;
  font-family: $text;
  font-size: $size-4;
  font-weight: 400;
  letter-spacing: 0.2rem;
  margin-top: 0;
  padding: 0 0 $size-1 0;
}

.project__description {
  color: $white;
  font-family: $text;
  font-size: $size-3;
  margin-top: 0;
  @media (min-width: $breakpoint-2) {
    font-size: $size-4;
  }
}

.project__tools-label {
  color: $primary-2;
  font-weight: 300;
}
